import { NextResponse } from "next/server";
import { createWalletClient, http, parseEther, publicActions } from "viem";
import { privateKeyToAccount } from "viem/accounts";
import { baseSepolia } from "viem/chains";

/**
 *
 * @param req
 */
export async function POST(req: Request) {
  try {
    const { address } = await req.json();

    if (!address) {
      return NextResponse.json({ error: "Address is required" }, { status: 400 });
    }

    const pKey = process.env.FAUCET_PRIVATE_KEY;
    if (!pKey) {
      console.error("FAUCET_PRIVATE_KEY not set");
      return NextResponse.json({ error: "Faucet configuration error" }, { status: 500 });
    }

    // Setup Wallet Client
    const account = privateKeyToAccount(pKey as `0x${string}`);
    const client = createWalletClient({
      account,
      chain: baseSepolia,
      transport: http(),
    }).extend(publicActions);

    // 1. Send ETH (0.005 ETH)
    // @ts-expect-error - KZG needed for blob txs but this is simple transfer
    const hash = await client.sendTransaction({
      to: address,
      value: parseEther("0.005"),
    });

    // TODO: Send USDC (Day 5 requirement mostly, but good for testing)
    // const usdcAddress = '0x...';
    // await client.writeContract({ ... });

    return NextResponse.json({
      success: true,
      txHash: hash,
    });
  } catch (error: any) {
    console.error("Faucet Error:", error);
    return NextResponse.json({ error: error.message || "Internal Server Error" }, { status: 500 });
  }
}
